import { mapMutations, mapState } from 'vuex';
export default {
	data() {
		return {
			show:false,
			userInfo: null,
			js_code: null,
			encryptedData:null,
			iv:null
		};
	},
	
	computed:{...mapState(['shareMsg'])},
	onLoad() {
		wx.login({
			success: res => {
				console.log('微信登录', res);
				this.js_code = res.code;
			}
		});
	},
	methods: {
		...mapMutations(['setLogin', 'setWxInfo']),
		bindGetUserInfo(e) {
			//点击授权弹窗上面的按钮
			let that = this;
			console.log('bindGetUserInfo-------', e);
			this.show=true
			if (e.detail.userInfo) {
				//用户按了允许授权按钮
				wx.showToast({
					title: '数据加载中',
					icon: 'loading',
					duration: 1000
				});
				this.userInfo = e.detail.userInfo;
				this.encryptedData=e.detail.encryptedData;
				this.iv=e.detail.iv;
				let userInfo = e.detail.userInfo;
				that.setWxInfo(userInfo);
			} else {
				//用户按了拒绝按钮
				wx.showModal({
					title: '警告',
					content: '您点击了拒绝授权，将无法进入小程序，请授权之后再进入!!!',
					showCancel: false,
					confirmText: '返回授权',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击了“返回授权”');
						}
					}
				});
			}
		},
		getPhoneNumber(e) {
			console.log('getPhoneNumber-------', e);
			let that=this;
			if (e.detail.encryptedData) {
				that.$Fetch({
					url: 'login/xiaoLogin',
					data: {
						js_code: that.js_code,
						user_name: that.userInfo.nickName,
						head_img: that.userInfo.avatarUrl,
						encryptedData: that.encryptedData,
						iv: that.iv,
						phone_encryptedData:e.detail.encryptedData,
						phone_iv:e.detail.iv,
						server_people_id:that.shareMsg.server
					},
					debug: true,
					success: res => {
						console.log(res);
						uni.setStorage({
							key: 'TOKEN',
							data: res,
							success: function() {
								that.setLogin();
								uni.showToast({
									title: '登录成功',
									icon: 'success'
								});
								setTimeout(() => {
									uni.reLaunch({
										url: '/pages/index/index'
									});
								}, 1500);
							}
						});
					}
				});
			}else{
				//用户按了拒绝按钮
				wx.showModal({
					title: '警告',
					content: '您点击了拒绝授权，将无法进入小程序，请授权之后再进入!!!',
					showCancel: false,
					confirmText: '返回授权',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击了“返回授权”');
						}
					}
				});
			}
		}
	}
};
